* {
  font-family: "HKGroteskRegular";
  text-decoration: none;
  text-align: center;
  font-size: 12px;
}

li a {
  font-size: 12px;
  font-weight: bold;
  text-decoration: none;
  color: black;
}

.link {
  color: black;
}

.link:hover {
  color: black;
  text-decoration: none;
}

li a:hover {
  text-decoration: none;
  color: black;
}

h1 {
  font-family: "Bentham";
  font-weight: bold;
  letter-spacing: 15px;
}

ul {
  list-style-type: none;
}

.normalText {
  font-size: 12px;
}

.smallText {
  font-size: 10px;
}

.smallerText {
  font-size: 8px;
}

.foot {
  background-color: black;
  color: white;
}

.foot li {
  text-align: left;
  text-indent: 10px;
  margin-bottom: 15px;
  font-size: 10px;
  color: lightgray;
}

h2 {
  font-weight: bold;
  text-align: center;
  letter-spacing: 4px;
}

.menu-item {
  padding: 0 40px;
  margin: 5px 10px;
  user-select: none;
  cursor: pointer;
  border: 1px transparent solid;
}
.menu-item-wrapper {
  border: 1px transparent solid;
}

.scroll-menu-arrow {
  padding: 20px;
  cursor: pointer;
}

.scroll-menu-arrow--disabled {
  visibility: hidden;
}

#account:hover {
  cursor: pointer;
}

i:hover {
  color: rgb(73, 73, 73);
}

.cardHover:hover {
  box-shadow: 8px 8px 8px lightgray;
  cursor: pointer;
}

.dropDiv {
  box-shadow: 0px 0px 0px 0px lightgray;
  position: absolute;
  background-color: #fff;
  z-index: 1;
}

.dropDiv li {
  text-align: left;
  line-height: 30px;
}

.dropDiv h2 {
  letter-spacing: 4px;
}
